<script>
export default {
  name: 'CakeAnimation',
  props: {
    clickNumber: {
      type: Number
    }
  },
  computed: {
    clipPath () {
      if(this.clickNumber < 3) {
          return "polygon(0 0, 100% 0, 100% 100%, 0% 100%)";
      } else if (this.clickNumber == 3){
          return "polygon(0 0, 70% 0, 70% 100%, 0% 100%)";
      } else if (this.clickNumber == 4){
          return "polygon(0 0, 40% 0, 40% 100%, 0% 100%)";
      } else {
          return "polygon(0 0, 0% 0, 0% 100%, 0% 100%)";
      }
    }
  },
}

</script>

